:root {
    --color-main: #F23D4C;
    --color-secondary: #023E73;
    --color-main-alt:  #D93D59;
    --color-secondary-alt: #35648C;
    --color-back: #f2f2f2;
}
body {
    font-family: 'IBM Plex Mono', monospace;
    background-color: var(--color-back);
}
#header {
    text-align: center;
    color: var(--color-secondary);
    border: 2px solid var(--color-secondary-alt);
}
#content {
    display: flex;
    flex-direction: row;
}
textarea {
    border: 2px solid var(--color-main);
    margin: 2px;
    width: 40vw;
}